import { Component, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { ApiservicesService } from '../../provider/apiservices.service';
import { DataTransferService } from '../../services/data-transfer.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { URLS, APP_CONFIG } from '../../../../src/assets/constant';
import { Location } from '@angular/common';

@Component({
  selector: 'app-dynamic-iframe',
  templateUrl: './dynamic-iframe.page.html',
  styleUrls: ['./dynamic-iframe.page.scss'],
})

export class DynamicIframePage implements OnInit {

  iframeLink:any;

  constructor(public router:Router,public storageService:LocalStorageService,public zone:NgZone,
    public apiservices:ApiservicesService,public sendData:DataTransferService,public location:Location) {
    
    // below data is comming from Tab1Page,Tab2Page & SideMenuPage
    this.iframeLink=this.sendData.dynamicIframeData;
    console.log("get iframe link and data==", this.iframeLink);


  }




  ngOnInit() {}

  hideIframe(){
    this.zone.run(()=>{
      this.location.back();
    });
  }

}
